@import '../common/styles/saas/variables';
@import '../common/styles/saas/mixins';

.contactsBlock {
  background-color: $bgSecondColor;
  padding: 100px 0;

  .container {
    @include container();
    @include flex(column, flex-start, stretch, nowrap);

    .contactsArea {
      @include flex(row, space-between, stretch, nowrap);
      width: 100%;

      .contactInfo {
        @include flex(column, flex-start, stretch, nowrap);
        box-sizing: border-box;
        padding: 0;
        margin: 0;
        margin-left: 10px;
        color: #666;

        .infoItem {
          font-size: 16px;
          color: #666;
          margin-bottom: 50px;

          .icon {
            width: 50px;
            height: 50px;
            line-height: 50px;
            text-align: center;
            font-size: 16px;
            float: left;
            background: $headingColor;
            color: #eee;
            border-radius: 4px;
          }

          .infoContent {
            @include text($mainFont, 16px, 400, 1.2, $textColor);
            text-align: left;
            margin-left: 70px;
            color: $textColor;
          }

          p {
            margin: 0;
            font-size: 15px
          }
        }
      }

      .formSubmit {
        overflow: hidden;
        width: 100%;
        @include flex(column, flex-start, stretch, nowrap);
        margin-left: 40px;
        margin-right: 40px;

        .formStyle {
          overflow: hidden;

          input {
            width: 100%;
            padding: 10px;
            border: none;
            background: #f7f7f7;
            margin-bottom: 30px;

            &::-webkit-input-placeholder {
              color: $textColor;
              @include text($mainFont, 12px, 400, 1.2, $textColor);
            }
          }

          textarea {
            width: 100%;
            height: 140px;
            padding: 10px;
            border: none;
            background: #f7f7f7;
            margin-bottom: 30px;
            resize: none;

            &::-webkit-input-placeholder {
              color: $textColor;
              @include text($mainFont, 12px, 400, 1.2, $textColor);
            }

            &:-webkit-autofill {
              border: 1px solid #666666;
              -webkit-text-fill-color: #666666;
              -webkit-box-shadow: 0 0 0 1000px #666666 inset;
              transition: background-color 500s ease-in-out 0s;

              &:focus {
                border: 2px solid $bgSecondColor;
              }
            }
          }
          @media screen and (max-width: 470px) {
            .contactsArea {
              width: 100%;
            }
          }
        }
      }


    }

  }
}










